<template>
	<view class="content">
		<uni-list>
			<uni-list-item v-for="(item, index) in policeList" :key="index" :rightText="item.value" clickable link :to="goToPage(item)">
				<view slot="header" class="form-title">
					<text style="color:#999">网格警员{{ index + 1 }}</text>
					<uni-tag text="驳回" type="error" v-if="!item.status" style="margin-left: 10rpx;"></uni-tag>
				</view>
			</uni-list-item>
		</uni-list>
	</view>
</template>

<script>
import { taskAPI } from '@/api/index.js';
export default {
	data() {
		return {
			policeList: [],
			id: ''
		};
	},
	onLoad(options) {
		this.id = options.id;
	},
	onShow() {
		if (this.id) {
			this.getGridPoliceById(this.id);
		}
	},
	methods: {
		getGridPoliceById(id) {
			taskAPI.getGridPoliceById({ id }).then(res => {
				if (res.code == 200) {
					this.policeList = res.data.valueVoList;
				}
			});
		},
		goToPage(item) {
			if (item.status) {
				return `../patrolTask/troubleList?id=${this.id}&userId=${item.key}`;
			} else {
				return `../patrolTask/rejectReason?id=${this.id}&gridId=${item.key}`;
			}
		}
	}
};
</script>

<style scoped></style>
